import { Line } from "react-chartjs-2";
import { Chart as ChartJS } from "chart.js/auto";

function Graph({ values, tipo }) {
  const { avalues, rvalues, errorRel, errorAbs, labels } = values;

  let datasets = [];

  if (tipo == "ecuaciones") {
    datasets = [
      {
        label: "Aproximado",
        data: avalues,
        backgroundColor: ["rgba(249, 59, 59, 1)"],
        borderColor: ["rgba(249, 59, 59, 0.6)"],
        borderWidth: 1,
      },
      {
        label: "Real",
        data: rvalues,
        backgroundColor: ["rgba( 115, 198, 182 , 1)"],
        borderColor: ["rgba(115, 198, 182, 0.6)"],
        borderWidth: 1,
      },
    ];
  } else if (tipo == "er") {
    datasets = [
      {
        label: "Error relativo (%)",
        data: errorRel,
        backgroundColor: ["rgba(255, 206, 86, 1)"],
        borderColor: ["rgba(255, 206, 86, 0.6)"],
        borderWidth: 1,
      },
    ];
  } else {
    datasets = [
      {
        label: "Error absoluto",
        data: errorAbs,
        backgroundColor: ["rgba(255, 206, 86, 1)"],
        borderColor: ["rgba(255, 206, 86, 0.6)"],
        borderWidth: 1,
      },
    ];
  }

  return (
    <>
      <Line
        height={340}
        options={{
          maintainAspectRatio: false,
        }}
        data={{
          labels: labels,
          datasets: datasets,
        }}
      />
    </>
  );
}

export default Graph;
