function Table({ values }) {
  const { avalues, rvalues, errorRel, errorAbs, labels } = values;
  let totalError = 0;

  const prom = () => {
    return parseFloat(totalError / errorRel.length).toFixed(4);
  };

  return (
    <>
      <table className="table">
        <thead>
          <tr>
            <th scope="col">#</th>
            <th scope="col">t</th>
            <th scope="col">Real</th>
            <th scope="col">Aproximado</th>
            <th scope="col">Error absoluto</th>
            <th scope="col">Error relativo</th>
          </tr>
        </thead>
        <tbody>
          {errorRel.map((data, index) => {
            totalError += data;
            return (
              <tr key={labels[index]}>
                <th>{index}</th>
                <th scope="row">{labels[index]}</th>
                <td>{rvalues[index].toFixed(6)}</td>
                <td>{avalues[index].toFixed(6)}</td>
                <td className={data > 2 ? "text-danger" : "text-success"}>
                  {errorAbs[index].toExponential(4)}
                </td>
                <td className={data > 2 ? "text-danger" : "text-success"}>
                  {data.toExponential(4)}
                </td>
              </tr>
            );
          })}
        </tbody>
      </table>

      <span className={prom() > 2 ? "text-danger" : "text-success"}>
        Error promedio: {prom()}%
      </span>
    </>
  );
}

export default Table;
