const f = (w) => w;

/* Modificable */

const g = (t, y, w) => 2 * w - 2 * y;

const C1 = (t, v) => Math.cos(t) * Math.exp(t);

const C2 = (t, v) => Math.sin(t) * Math.exp(t);

const yp = (t) => 0;

const realData = (t, c1, c2) => c1 * C1(t) + c2 * C2(t) + yp(t);

/*  */

const data = (li, ls, n, vi, vs) => {
  const { C1, C2 } = calcC(li, ls, vi, vs);

  let h = Math.abs(ls - li) / n;

  /* Interpolaciones */

  let w1 = interpolacion(li, ls, vi, h, vs, 10, -10)
  let w2 = interpolacion(li, ls, vi, h, vs, w1 + (Math.abs(w1) / 5), w1 - (Math.abs(w1) / 5))
  let w = interpolacion(li, ls, vi, h, vs, w2 + (Math.abs(w1) / 8), w2 - (Math.abs(w1) / 8))
  
  /*  */

  let y = vi;
  let t = li;

  let aprox = [y];
  let real = [y];
  let err = [0];
  let era = [0];
  let labels = [li];

  while (t < ls) {
    const l1 = f(w);
    const k1 = g(t, y, w);
    const l2 = f(l1 + (h / 2) * k1);
    const k2 = g(t + h / 2, y + (h / 2) * l1, l2);
    const l3 = f(l2 + (h / 2) * k2);
    const k3 = g(t + h / 2, y + (h / 2) * l1, l3);
    const l4 = f(l3 + (h / 2) * k3);
    const k4 = g(t + h / 2, y + (h / 2) * l2, l4);

    y = y + (h / 6) * (l1 + 2 * l2 + 2 * l3 + l4);
    w = w + (h / 6) * (k1 + 2 * k2 + 2 * k3 + k4);

    aprox.push(y);

    t += h;
    labels.push(parseFloat(t).toFixed(4));

    real.push(realData(t, C1, C2));

    let error = Math.abs(real[real.length - 1] - aprox[aprox.length - 1]);
    era.push(error);
    err.push((error / real[real.length - 1]) * 100);
  }

  return {
    avalues: aprox,
    rvalues: real,
    errorRel: err,
    errorAbs: era,
    labels: labels,
  };
};

const calcW = (li, ls, vi, h, w) => {
  let y = vi;
  let t = li;

  while (t < ls) {
    const l1 = f(w);
    const k1 = g(t, y, w);
    const l2 = f(l1 + (h / 2) * k1);
    const k2 = g(t + h / 2, y + (h / 2) * l1, l2);
    const l3 = f(l2 + (h / 2) * k2);
    const k3 = g(t + h / 2, y + (h / 2) * l1, l3);
    const l4 = f(l3 + (h / 2) * k3);
    const k4 = g(t + h / 2, y + (h / 2) * l2, l4);

    y = y + (h / 6) * (l1 + 2 * l2 + 2 * l3 + l4);
    w = w + (h / 6) * (k1 + 2 * k2 + 2 * k3 + k4);

    t += h;
  }

  return y;
};

const interpolacion = (li, ls, vi, h, vs, w1, w2) => {
  let vf1 = calcW(li, ls, vi, h, w1);

  let vf2 = calcW(li, ls, vi, h, w2);

  return w1 + ((w2 - w1) / (vf2 - vf1)) * (vs - vf1);
};

const calcC = (li, ls, vi, vs) => {
  const linSystem = require("linear-solve");

  const A = [
    [C1(li, vi), C2(li, vi)],
    [C1(ls, vs), C2(ls, vs)],
  ];

  const B = [vi - yp(li), vs - yp(ls)];

  const Cs = linSystem.solve(A, B);

  return { C1: Cs[0], C2: Cs[1] };
};

export default data;
