const f = (w) => w;

/* Modificable */

const g = (t, y, w) => 2 * Math.exp(t) - 2 * w - y;

const C1 = (t, v) => Math.exp(-t);

const C2 = (t, v) => t * Math.exp(-t);

const yp = (t) => Math.exp(t) / 2;

const ypd = (t) => Math.exp(t) / 2;

const C1d = (t, vd) => -Math.exp(-t);

const C2d = (t, vd) => Math.exp(-t) * (1 - t);

const realData = (t, c1, c2) => c1 * C1(t) + c2 * C2(t) + yp(t);

/*  */

const data = (ls, n, p, v, vd) => {
  const { C1, C2 } = calcC(p, v, vd);

  let h = Math.abs(ls - p) / n;

  let y = v;
  let w = vd;
  let t = p;

  let aprox = [y];
  let real = [y];
  let err = [0];
  let era = [0];
  let labels = [p];

  while (t < ls) {
    const l1 = f(w);
    const k1 = g(t, y, w);
    const l2 = f(l1 + (h / 2) * k1);
    const k2 = g(t + h / 2, y + (h / 2) * l1, l2);
    const l3 = f(l2 + (h / 2) * k2);
    const k3 = g(t + h / 2, y + (h / 2) * l1, l3);
    const l4 = f(l3 + (h / 2) * k3);
    const k4 = g(t + h / 2, y + (h / 2) * l2, l4);

    y = y + (h / 6) * (l1 + 2 * l2 + 2 * l3 + l4);
    w = w + (h / 6) * (k1 + 2 * k2 + 2 * k3 + k4);

    aprox.push(y);

    t += h;
    labels.push(parseFloat(t).toFixed(4));

    real.push(realData(t, C1, C2));

    let error = Math.abs(real[real.length - 1] - aprox[aprox.length - 1]);
    era.push(error);
    err.push((error / real[real.length - 1]) * 100);
  }

  return {
    avalues: aprox,
    rvalues: real,
    errorRel: err,
    errorAbs: era,
    labels: labels,
  };
};

const calcC = (p, v, vd) => {
  const linSystem = require("linear-solve");

  const A = [
    [C1(p, v), C2(p, v)],
    [C1d(p, vd), C2d(p, vd)],
  ];

  const B = [v - yp(p), vd - ypd(p)];

  const Cs = linSystem.solve(A, B);

  return { C1: Cs[0], C2: Cs[1] };
};

export default data;
