import FormVI from "./FormVI";
import Modal from "./Modal";
import Table from "./Table";
import Graph from "./Graph";
import GraphSelector from "./GraphSelector";
import data from "../helpers/dataVI";
import { useState } from "react";

function VI() {
  const defaultValues = [10, 10, 0, 0, 1];
  /* [ls, n, p, v, vd] */

  const [numeros, setNumeros] = useState({
    ls: defaultValues[0],
    n: defaultValues[1],
    p: defaultValues[2],
    v: defaultValues[3],
    vd: defaultValues[4],
  });

  const [tipo, setTipo] = useState("ecuaciones");

  const { ls, n, p, v, vd } = numeros;

  const values = data(
    parseFloat(ls),
    parseFloat(n),
    parseFloat(p),
    parseFloat(v),
    parseFloat(vd)
  );

  return (
    <>
      <div className="row w-100">
        <FormVI numeros={numeros} setNumeros={setNumeros} />
        <div className="col-sm-9">
          <div className="h-100 w-100">
            <Graph values={values} tipo={tipo} />
          </div>
        </div>
      </div>
      <GraphSelector setTipo={setTipo} />
      <Modal>
        <Table values={values} />
      </Modal>
    </>
  );
}

export default VI;
