function GraphSelector({ setTipo }) {
  const buttonsChange = (e) => {
    setTipo([e.target.id]);
  };

  return (
    <div className="d-flex justify-content-center gap-2 mt-3 w-40">
      <div className="btn-group" data-toggle="buttons">
        <label className="btn btn-success" htmlFor="ecuaciones">
          <input
            type="radio"
            name="options"
            id="ecuaciones"
            onChange={buttonsChange}
            defaultChecked
          />{" "}
          Ecuaciones
        </label>
        <label className="btn btn-danger" htmlFor="er">
          <input type="radio" name="options" id="er" onChange={buttonsChange} />{" "}
          Error relativo
        </label>
        <label className="btn btn-danger" htmlFor="ea">
          <input type="radio" name="options" id="ea" onChange={buttonsChange} />{" "}
          Error absoluto
        </label>
      </div>
    </div>
  );
}

export default GraphSelector;
