import FormVF from "./FormVF";
import Modal from "./Modal";
import Table from "./Table";
import Graph from "./Graph";
import GraphSelector from "./GraphSelector";
import data from "../helpers/dataVF";
import { useState } from "react";

function VF() {
  const defaultValues = [0, Math.PI / 2, 10, 1, 1];
  /* [li, ls, n, vi, vs] */

  const [numeros, setNumeros] = useState({
    li: defaultValues[0],
    ls: defaultValues[1],
    n: defaultValues[2],
    vi: defaultValues[3],
    vs: defaultValues[4],
  });

  const [tipo, setTipo] = useState("ecuaciones");

  const { li, ls, n, vi, vs } = numeros;

  const values = data(li, ls, n, vi, vs);

  return (
    <>
      <div className="row w-100">
        <FormVF numeros={numeros} setNumeros={setNumeros} />
        <div className="col-sm-9">
          <div className="h-100 w-100">
            <Graph values={values} tipo={tipo} />
          </div>
        </div>
      </div>
      <GraphSelector setTipo={setTipo} />
      <Modal>
        <Table values={values} />
      </Modal>
    </>
  );
}

export default VF;
