import Input from "./Input";

function FormVF({ numeros, setNumeros }) {
  const { li, ls, n, vi, vs } = numeros;

  const inputs = [
    { id: "li", label: "Límite inferior", value: li },
    { id: "ls", label: "Limite superior", value: ls },
    { id: "n", label: "Número de saltos", value: n },
    { id: "vi", label: "Valor en el límite inferior", value: vi },
    { id: "vs", label: "Valor en el límite superior", value: vs },
  ];

  const updateValues = (element) => {
    if (element?.li ?? false) {
      if (element.li >= ls) element.ls = element.li + 1;
    }

    setNumeros({
      ...numeros,
      ...element,
    });
  };

  return (
    <div
      id="form"
      className="col-sm-3 d-flex justify-content-center flex-column mb-4"
    >
      {inputs.map(({ id, label, value }) => (
        <Input
          id={id}
          label={label}
          value={value}
          update={updateValues}
          key={id}
        />
      ))}
      <button
        className="btn btn-primary"
        data-bs-toggle="modal"
        data-bs-target="#tableModal"
      >
        Ver tabla
      </button>
    </div>
  );
}

export default FormVF;
