import { useState } from "react";
import Header from "./components/Header";
import VF from "./components/VF";
import VI from "./components/VI";

function App() {
  const [mode, setMode] = useState("Valores iniciales");

  return (
    <div className="d-flex justify-content-center align-items-center flex-column flex-wrap pt-4">
      <Header mode={mode} setMode={setMode} />
      {mode === "Valores iniciales" ? <VI /> : <VF />}
    </div>
  );
}

export default App;
