import Input from "./Input";

function FormVI({ numeros, setNumeros }) {
  const { ls, n, p, v, vd } = numeros;

  const inputs = [
    { id: "ls", label: "Límite superior", value: ls },
    { id: "n", label: "Numero de saltos", value: n },
    { id: "p", label: "Defina un punto", value: p },
    { id: "v", label: "Valor en el punto dado", value: v },
    { id: "vd", label: "Valor en la derivada del punto dado", value: vd },
  ];

  const updateValues = (element) => {
    if (element?.p ?? false) {
      if (element.p >= ls) element.ls = element.p + 1;
    }

    setNumeros({
      ...numeros,
      ...element,
    });
  };

  return (
    <div
      id="form"
      className="col-sm-3 d-flex justify-content-center flex-column mb-4"
    >
      {inputs.map(({ id, label, value }) => (
        <Input
          id={id}
          label={label}
          value={value}
          update={updateValues}
          key={id}
        />
      ))}
      <button
        className="btn btn-primary"
        data-bs-toggle="modal"
        data-bs-target="#tableModal"
      >
        Ver tabla
      </button>
    </div>
  );
}

export default FormVI;
